"use client";
import { useState, useEffect } from "react";
import { createBrowserClient } from "@supabase/ssr";
import {
  Bell,
  CheckCircle,
  MapPin,
  Utensils,
  Plus,
  RefreshCw,
  LogOut,
  X,
  Image as ImageIcon,
  DollarSign,
  AlignLeft,
  Tag,
  Loader2,
  Trash2,
  ChefHat,
} from "lucide-react";

export default function AdminDashboard() {
  const [activeTab, setActiveTab] = useState("orders");
  const [orders, setOrders] = useState<any[]>([]);
  const [products, setProducts] = useState<any[]>([]);

  const [isAddOpen, setIsAddOpen] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [newProduct, setNewProduct] = useState({
    name: "",
    description: "",
    price: "",
    category: "menu",
    image_url: "",
  });

  const router = useRouter();
  const supabase = createBrowserClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!,
  );

  // --- LOGOUT ---
  const handleLogout = async () => {
    if (!window.confirm("¿Cerrar sesión de cocina?")) return;
    await supabase.auth.signOut();
    router.push("/admin");
  };

  // --- FETCHING ---
  useEffect(() => {
    const checkSession = async () => {
      const {
        data: { session },
      } = await supabase.auth.getSession();
      if (!session) router.push("/admin");
    };
    checkSession();
    fetchOrders();
    fetchProducts();

    const channel = supabase
      .channel("orders_realtime")
      .on(
        "postgres_changes",
        { event: "INSERT", schema: "public", table: "orders" },
        (payload) => {
          setOrders((prev) => [payload.new, ...prev]);
          new Audio(
            "https://codeskulptor-demos.commondatastorage.googleapis.com/pang/pop.mp3",
          )
            .play()
            .catch(() => {});
        },
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  const fetchOrders = async () => {
    const { data } = await supabase
      .from("orders")
      .select("*")
      .order("created_at", { ascending: false });
    if (data) setOrders(data);
  };

  const fetchProducts = async () => {
    const { data } = await supabase
      .from("products")
      .select("*")
      .order("created_at", { ascending: false });
    if (data) setProducts(data);
  };

  // --- ACCIONES ---
  const handleAddProduct = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    const { data, error } = await supabase
      .from("products")
      .insert([
        {
          name: newProduct.name,
          description: newProduct.description,
          price: parseFloat(newProduct.price),
          category: newProduct.category,
          image_url: newProduct.image_url || null,
          is_available: true,
        },
      ])
      .select()
      .single();

    if (error) alert("Error: " + error.message);
    else {
      setProducts([data, ...products]);
      setIsAddOpen(false);
      setNewProduct({
        name: "",
        description: "",
        price: "",
        category: "menu",
        image_url: "",
      });
    }
    setIsSubmitting(false);
  };

  const toggleProductStatus = async (id: string, currentStatus: boolean) => {
    setProducts(
      products.map((p) =>
        p.id === id ? { ...p, is_available: !currentStatus } : p,
      ),
    );
    await supabase
      .from("products")
      .update({ is_available: !currentStatus })
      .eq("id", id);
  };

  const handleDeleteProduct = async (id: string) => {
    if (!confirm("¿Borrar este plato?")) return;
    setProducts(products.filter((p) => p.id !== id));
    await supabase.from("products").delete().eq("id", id);
  };

  const markOrderDelivered = async (id: string) => {
    setOrders(
      orders.map((o) => (o.id === id ? { ...o, status: "delivered" } : o)),
    );
    await supabase.from("orders").update({ status: "delivered" }).eq("id", id);
  };

  return (
    <div className="min-h-screen bg-[#F3F4F6] pb-24 font-sans text-gray-900 selection:bg-black selection:text-white">
      {/* --- NUEVA BARRA DE NAVEGACIÓN (Sticky Top) --- */}
      {/* Fusionamos los Tabs con los botones de acción para eliminar el Header grande */}
      <div className="sticky top-0 z-40 pt-6 pb-2 px-4 bg-[#F3F4F6]/90 backdrop-blur-xl flex items-center justify-center gap-3">
        {/* TABS (Izquierda/Centro) */}
        <div className="flex-1 max-w-md p-1.5 bg-white/80 backdrop-blur-md rounded-[1.2rem] shadow-sm border border-white/60 flex">
          <button
            onClick={() => setActiveTab("orders")}
            className={`flex-1 py-3 rounded-xl text-sm font-bold flex items-center justify-center gap-2 transition-all duration-300 ${
              activeTab === "orders"
                ? "bg-gray-900 text-white shadow-lg shadow-gray-900/20 scale-[1.02]"
                : "text-gray-400 hover:text-gray-600 hover:bg-gray-50"
            }`}
          >
            <Bell
              size={16}
              className={activeTab === "orders" ? "fill-white" : ""}
            />
            Pedidos
            {orders.filter((o) => o.status !== "delivered").length > 0 && (
              <span className="bg-red-500 text-white text-[10px] min-w-[18px] h-[18px] flex items-center justify-center rounded-full shadow-sm border border-gray-900">
                {orders.filter((o) => o.status !== "delivered").length}
              </span>
            )}
          </button>
          <button
            onClick={() => setActiveTab("menu")}
            className={`flex-1 py-3 rounded-xl text-sm font-bold flex items-center justify-center gap-2 transition-all duration-300 ${
              activeTab === "menu"
                ? "bg-gray-900 text-white shadow-lg shadow-gray-900/20 scale-[1.02]"
                : "text-gray-400 hover:text-gray-600 hover:bg-gray-50"
            }`}
          >
            <Utensils
              size={16}
              className={activeTab === "menu" ? "fill-white" : ""}
            />
            Menú
          </button>
        </div>

        {/* ACCIONES (Derecha - Pequeñas) */}
        <div className="flex gap-2">
          <button
            onClick={() => window.location.reload()}
            className="p-3 bg-white text-gray-400 rounded-2xl hover:text-gray-900 transition-colors shadow-sm active:scale-95"
          >
            <RefreshCw size={20} />
          </button>
          <button
            onClick={handleLogout}
            className="p-3 bg-white text-red-400 rounded-2xl hover:text-red-600 hover:bg-red-50 transition-colors shadow-sm active:scale-95"
          >
            <LogOut size={20} />
          </button>
        </div>
      </div>

      {/* --- CONTENIDO: PEDIDOS --- */}
      {activeTab === "orders" && (
        <div className="px-4 mt-2 space-y-5 animate-in fade-in slide-in-from-bottom-4 duration-500 max-w-2xl mx-auto">
          {orders.length === 0 && (
            <div className="text-center py-20 flex flex-col items-center opacity-40">
              <div className="w-20 h-20 bg-gray-200 rounded-full flex items-center justify-center mb-4">
                <Bell size={32} className="text-gray-400" />
              </div>
              <p className="font-bold text-gray-500 text-lg">
                Sin pedidos pendientes
              </p>
              <p className="text-sm text-gray-400">
                Todo tranquilo en la cocina
              </p>
            </div>
          )}

          {orders.map((order) => (
            <div
              key={order.id}
              className={`bg-white rounded-[1.5rem] p-6 shadow-sm border border-gray-100 relative overflow-hidden transition-all hover:shadow-md ${order.status === "delivered" ? "opacity-60 bg-gray-50 grayscale" : ""}`}
            >
              {/* Badge Hora */}
              <div className="absolute top-0 right-0 bg-gray-100 px-4 py-2 rounded-bl-2xl text-xs font-bold text-gray-500 border-b border-l border-white">
                {new Date(order.created_at).toLocaleTimeString([], {
                  hour: "2-digit",
                  minute: "2-digit",
                })}
              </div>

              {/* Cliente */}
              <div className="mb-5 pr-16">
                <h3 className="font-black text-xl text-gray-900 tracking-tight">
                  {order.customer_name}
                </h3>
                <div className="flex items-center gap-2 text-gray-500 text-sm font-semibold mt-1">
                  <div className="bg-orange-50 p-1 rounded-md">
                    <MapPin size={14} className="text-orange-500" />
                  </div>
                  {order.customer_office}
                </div>
              </div>

              {/* Items */}
              <div className="bg-gray-50/50 rounded-2xl p-4 mb-5 space-y-3 border border-gray-100">
                {order.items.map((item: any, i: number) => (
                  <div key={i} className="flex gap-3 text-sm items-start">
                    <span className="font-black text-xs bg-white px-2 py-1 rounded-lg border border-gray-200 min-w-[28px] text-center shadow-sm text-gray-900">
                      {item.qty}x
                    </span>
                    <span className="text-gray-700 font-medium leading-snug pt-0.5">
                      {item.name}
                    </span>
                  </div>
                ))}
              </div>

              {/* Footer Info */}
              <div className="flex justify-between items-center mb-5 pb-5 border-b border-dashed border-gray-200">
                <div className="text-2xl font-black text-gray-900 tracking-tighter">
                  S/ {order.total_amount}
                </div>
                <div
                  className={`text-[10px] font-bold px-3 py-1.5 rounded-full uppercase tracking-wide flex items-center gap-2 border ${order.payment_method === "yape" ? "bg-purple-50 text-purple-700 border-purple-100" : "bg-emerald-50 text-emerald-700 border-emerald-100"}`}
                >
                  {order.payment_method === "yape" ? (
                    <div className="w-2 h-2 rounded-full bg-purple-500 animate-pulse" />
                  ) : (
                    <div className="w-2 h-2 rounded-full bg-emerald-500" />
                  )}
                  {order.payment_method === "yape" ? "Yape" : "Efectivo"}
                </div>
              </div>

              {/* BOTÓN ACCIÓN: VERDE ESMERALDA VIBRANTE */}
              {order.status !== "delivered" ? (
                <button
                  onClick={() => markOrderDelivered(order.id)}
                  className="w-full bg-emerald-500 text-white py-4 rounded-2xl font-bold text-sm flex items-center justify-center gap-2 active:scale-95 transition-all shadow-xl shadow-emerald-200 hover:bg-emerald-600 hover:shadow-emerald-300"
                >
                  <CheckCircle size={20} strokeWidth={3} />
                  MARCAR COMO ENTREGADO
                </button>
              ) : (
                <div className="w-full bg-gray-100 text-gray-400 py-3 rounded-2xl font-bold text-xs uppercase flex items-center justify-center gap-2 border border-gray-200">
                  <CheckCircle size={16} /> Pedido Completado
                </div>
              )}
            </div>
          ))}
        </div>
      )}

      {/* --- CONTENIDO: MENÚ --- */}
      {activeTab === "menu" && (
        <div className="px-4 mt-2 space-y-4 animate-in fade-in slide-in-from-bottom-4 duration-500 max-w-2xl mx-auto">
          {/* BOTÓN AGREGAR: NEGRO ELEGANTE (Tipo Apple) */}
          <button
            onClick={() => setIsAddOpen(true)}
            className="group w-full bg-gray-900 text-white py-4 rounded-[1.2rem] font-bold shadow-2xl shadow-gray-300 flex items-center justify-center gap-3 active:scale-95 transition-all hover:bg-black"
          >
            <div className="bg-gray-700 p-1 rounded-full group-hover:bg-gray-600 transition-colors">
              <Plus size={16} />
            </div>
            <span>Agregar Nuevo Plato</span>
          </button>

          {products.map((product) => (
            <div
              key={product.id}
              className={`bg-white p-3 rounded-2xl shadow-sm border flex items-center gap-4 transition-all ${product.is_available ? "border-gray-100 hover:border-gray-300" : "border-gray-100 bg-gray-50 opacity-60"}`}
            >
              {/* Imagen */}
              <div className="relative w-16 h-16 shrink-0">
                <img
                  src={
                    product.image_url ||
                    "https://images.unsplash.com/photo-1546069901-ba9599a7e63c"
                  }
                  alt={product.name}
                  className={`w-full h-full rounded-xl object-cover bg-gray-100 shadow-inner ${!product.is_available && "grayscale"}`}
                />
              </div>

              {/* Info */}
              <div className="flex-1 min-w-0">
                <div className="flex justify-between items-start">
                  <h3
                    className={`font-bold leading-tight truncate pr-2 text-base ${!product.is_available ? "text-gray-500 line-through" : "text-gray-900"}`}
                  >
                    {product.name}
                  </h3>
                  <button
                    onClick={() => handleDeleteProduct(product.id)}
                    className="text-gray-300 hover:text-red-500 p-2 -mt-2 -mr-2 rounded-full hover:bg-red-50 transition-colors"
                  >
                    <Trash2 size={16} />
                  </button>
                </div>
                <p className="text-xs text-gray-500 font-mono mt-1 font-bold">
                  S/ {product.price}
                </p>
              </div>

              {/* Switch Toggle (iOS Style) */}
              <button
                onClick={() =>
                  toggleProductStatus(product.id, product.is_available)
                }
                className={`w-14 h-8 rounded-full p-1 transition-all duration-300 relative shadow-inner ${product.is_available ? "bg-emerald-500" : "bg-gray-200"}`}
              >
                <div
                  className={`w-6 h-6 bg-white rounded-full shadow-md transition-transform duration-300 flex items-center justify-center ${product.is_available ? "translate-x-6" : "translate-x-0"}`}
                >
                  {product.is_available && (
                    <div className="w-1.5 h-1.5 bg-emerald-500 rounded-full" />
                  )}
                </div>
              </button>
            </div>
          ))}
        </div>
      )}

      {/* --- MODAL AGREGAR --- */}
      {isAddOpen && (
        <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center p-0 sm:p-4 bg-gray-900/60 backdrop-blur-md transition-opacity">
          <div className="bg-white w-full max-w-md rounded-t-[2rem] sm:rounded-[2rem] p-8 h-[90vh] sm:h-auto overflow-y-auto animate-in slide-in-from-bottom-10 shadow-2xl">
            <div className="flex justify-between items-center mb-8">
              <h2 className="text-2xl font-black text-gray-900 tracking-tight">
                Nuevo Plato
              </h2>
              <button
                onClick={() => setIsAddOpen(false)}
                className="p-2 bg-gray-100 rounded-full hover:bg-gray-200 transition-colors"
              >
                <X size={24} />
              </button>
            </div>

            <form onSubmit={handleAddProduct} className="space-y-6">
              {/* Nombre */}
              <div className="space-y-2">
                <label className="text-xs font-bold text-gray-400 uppercase tracking-wider ml-1">
                  Nombre del Plato
                </label>
                <div className="relative group">
                  <Utensils
                    className="absolute left-4 top-4 text-gray-400 group-focus-within:text-black transition-colors"
                    size={20}
                  />
                  <input
                    required
                    value={newProduct.name}
                    onChange={(e) =>
                      setNewProduct({ ...newProduct, name: e.target.value })
                    }
                    placeholder="Ej: Arroz con Pato"
                    className="w-full pl-12 p-4 bg-gray-50 border-2 border-transparent focus:bg-white focus:border-black rounded-2xl font-bold outline-none transition-all placeholder:font-medium placeholder:text-gray-400"
                  />
                </div>
              </div>

              <div className="space-y-2">
                <label className="text-xs font-bold text-gray-400 uppercase tracking-wider ml-1">
                  Ingredientes
                </label>
                <div className="relative group">
                  <AlignLeft
                    className="absolute left-4 top-4 text-gray-400 group-focus-within:text-black transition-colors"
                    size={20}
                  />
                  <input
                    value={newProduct.description}
                    onChange={(e) =>
                      setNewProduct({
                        ...newProduct,
                        description: e.target.value,
                      })
                    }
                    placeholder="Arroz + Pato + Sarsa"
                    className="w-full pl-12 p-4 bg-gray-50 border-2 border-transparent focus:bg-white focus:border-black rounded-2xl font-bold outline-none transition-all placeholder:font-medium placeholder:text-gray-400"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <label className="text-xs font-bold text-gray-400 uppercase tracking-wider ml-1">
                    Precio
                  </label>
                  <div className="relative group">
                    <DollarSign
                      className="absolute left-4 top-4 text-gray-400 group-focus-within:text-black transition-colors"
                      size={20}
                    />
                    <input
                      required
                      type="number"
                      step="0.10"
                      value={newProduct.price}
                      onChange={(e) =>
                        setNewProduct({ ...newProduct, price: e.target.value })
                      }
                      placeholder="25.00"
                      className="w-full pl-12 p-4 bg-gray-50 border-2 border-transparent focus:bg-white focus:border-black rounded-2xl font-bold outline-none transition-all placeholder:font-medium placeholder:text-gray-400"
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <label className="text-xs font-bold text-gray-400 uppercase tracking-wider ml-1">
                    Categoría
                  </label>
                  <div className="relative group">
                    <Tag
                      className="absolute left-4 top-4 text-gray-400 group-focus-within:text-black transition-colors"
                      size={20}
                    />
                    <select
                      value={newProduct.category}
                      onChange={(e) =>
                        setNewProduct({
                          ...newProduct,
                          category: e.target.value,
                        })
                      }
                      className="w-full pl-12 p-4 bg-gray-50 border-2 border-transparent focus:bg-white focus:border-black rounded-2xl font-bold outline-none transition-all appearance-none cursor-pointer"
                    >
                      <option value="menu">Menú</option>
                      <option value="diet">Dieta</option>
                      <option value="extra">Extra</option>
                    </select>
                  </div>
                </div>
              </div>

              <div className="space-y-1">
                <label className="text-xs font-bold text-gray-500 uppercase ml-1">
                  Foto URL
                </label>
                <div className="relative">
                  <ImageIcon
                    className="absolute left-4 top-4 text-gray-400"
                    size={18}
                  />
                  <input
                    value={newProduct.image_url}
                    onChange={(e) =>
                      setNewProduct({
                        ...newProduct,
                        image_url: e.target.value,
                      })
                    }
                    placeholder="https://..."
                    className="w-full pl-12 p-4 bg-gray-50 border-2 border-transparent focus:bg-white focus:border-black rounded-2xl font-bold outline-none transition-all placeholder:font-medium placeholder:text-gray-400 text-xs"
                  />
                </div>
              </div>

              <button
                type="submit"
                disabled={isSubmitting}
                className="w-full bg-gray-900 text-white py-5 rounded-2xl font-black text-lg shadow-xl shadow-gray-200 flex items-center justify-center gap-3 mt-6 hover:bg-black active:scale-[0.98] transition-all"
              >
                {isSubmitting ? (
                  <Loader2 className="animate-spin" />
                ) : (
                  <>
                    <Plus size={24} /> PUBLICAR PLATO
                  </>
                )}
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
